@import url(https://fonts.google.com/specimen/Rubik);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.user {
  background-color: hsl(235, 46%, 20%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: white;
}

.user .user-profile {
  background-color: hsl(235, 45%, 61%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  height: 70%;
  width: 100%;
  border-radius: 12px;
}

.user .user-profile .user-avatar > div {
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid white;
  width: 72px;
}

.user .user-profile .user-avatar > div > img {
  width: 70px;
}

.time-frames-links {
  list-style-type: none;
  padding-left: 20px;
  padding-bottom: 20px;
}

.time-frames-links li {
  display: block;
  margin-top: 5px;
  cursor: pointer;
  color: hsl(236, 100%, 87%);
}

.time-frames-links li:hover {
  color:  white;
}

@media(max-width: 852px) {
  .user .user-profile {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 20px;
    padding-left: 5px;
    padding-bottom: 20px;
    height: 60%;
    width: 100%;
  }

  .time-frames {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
  }

  .time-frames-links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70%;
  }
}
.work {
    position: relative;
    background-color: hsl(15, 100%, 70%);
}

.work > img {
    position: absolute;
    top: -1px;
    right: 15px;
}

.work-info {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 75%;
    background-color: hsl(235, 46%, 20%);
    color: white;
    border-radius: 8px;
}

.work-info:hover {
    transition: 0.5s ease;
    background-color: hsl(235, 45%, 61%);
}

.name-cont {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
}

.hours-cont {
    width: 80%;
    margin: auto;
    margin-top: 20px;
}

.hours-cont > div {
    color: hsl(236, 100%, 87%);
}

@media(max-width: 852px) {
    .work-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .hours-cont {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0px;
    }
}
